/*******************************************************************************
                            Common-CSS
********************************************************************************/
.nav-container, .nav-bar-sub-container, .nav-bar-main-menu-container, .nav-bar-main-menu-container a, .nav-bar-account-container, .nav-bar-account-text-container, .nav-bar-arrow-icon {
  display: flex; }

.nav-container, .nav-bar-sub-container, .nav-bar-account-text-container, .nav-bar-arrow-icon {
  justify-content: center; }

.nav-bar-account-container {
  justify-content: space-between; }

.nav-bar-sub-container, .nav-bar-main-menu-container a, .nav-bar-account-container, .nav-bar-account-text-container, .nav-bar-arrow-icon {
  align-items: center; }

.nav-bar-notification {
  position: relative; }

.nav-bar-notification-no {
  position: absolute; }

/*******************************************************************************
                            APP
********************************************************************************/
.app-container {
  background-image: linear-gradient(to bottom, #eef9ff, #ade1ff); }

.card-wrapper {
  display: flex;
  justify-content: space-around;
  width: 70%;
  margin: 0 auto;
  flex-wrap: wrap;
  margin-bottom: 35px; }

.nav-bar-container {
  z-index: 99999;
  position: fixed;
  width: 100%; }

.nav-container {
  padding: 1.6%; }

.nav-bar-sub-container {
  width: 90%;
  justify-content: space-between; }

#menu-toggle {
  display: none; }

.nav-bar-label-toggle {
  display: none; }

.nav-bar-main-menu-container a {
  cursor: pointer;
  line-height: 1.33;
  font-weight: 700;
  letter-spacing: 1.9px;
  padding-right: 22px; }

.nav-bar-main-menu-container a:hover {
  color: white; }

.nav-bar-notification {
  margin-right: 10px;
  cursor: pointer; }

.nav-bar-leaderboard-icon {
  cursor: pointer;
  margin-right: 20px; }

.nav-bar-notification-no {
  top: -9px;
  right: -9px;
  background: #ff677f;
  padding: 2px 2px 2px 2px;
  font-size: 10px;
  border-radius: 50%;
  width: 13px;
  text-align: center;
  font-weight: 800;
  color: white; }

.nav-bar-account-container {
  background-color: #71aef3;
  width: 104px;
  border-radius: 3px;
  height: 34px;
  margin-left: 10px;
  cursor: pointer; }

.nav-bar-account-text-container {
  font-size: 17px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.2px;
  color: #ffffff;
  text-align: center;
  height: inherit;
  border-radius: 3px; }

.nav-bar-account-container-skill-icon {
  margin-left: 8px; }

.nav-bar-account-image {
  width: 34px;
  height: 34px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px; }

.nav-bar-arrow-icon {
  margin-left: 2px;
  width: 20px; }

@media only screen and (max-width: 1000px) {
  .nav-bar-sub-container {
    flex-wrap: wrap;
    justify-content: space-between; }
  .nav-bar-label-toggle {
    display: block;
    cursor: pointer; }
    .nav-bar-label-toggle span {
      position: relative; }
    .nav-bar-label-toggle span::before, .nav-bar-label-toggle span::after {
      position: absolute; }
    .nav-bar-label-toggle span::before, .nav-bar-label-toggle span::after {
      cursor: pointer;
      height: 3px;
      width: 30px;
      background: #fff;
      display: block;
      content: '';
      transition: all 0.4s; }
    .nav-bar-label-toggle span:before {
      top: -8px; }
    .nav-bar-label-toggle span:after {
      bottom: -8px; }
  #menu-toggle:checked ~ .label-toggle span {
    background-color: transparent; }
  #menu-toggle:checked ~ .nav-bar-label-toggle span:before,
  #menu-toggle:checked ~ .nav-bar-label-toggle span:after {
    top: 0; }
  #menu-toggle:checked ~ .nav-bar-label-toggle span:before {
    transform: rotate(45deg); }
  #menu-toggle:checked ~ .nav-bar-label-toggle span:after {
    transform: translateY(-6px) rotate(-45deg);
    top: 6px; }
  .nav-bar-main-menu-container {
    display: block;
    height: 0;
    visibility: hidden;
    opacity: 0;
    text-align: center;
    width: 100%; }
    .nav-bar-main-menu-container a {
      display: block;
      font-size: 10px;
      padding: 10px 0;
      font-size: 14px; }
  #menu-toggle:checked ~ .nav-bar-main-menu-container {
    opacity: 1;
    height: inherit;
    visibility: visible;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center; } }
